<template>
	<view class="container">
		<view class="address-list">
			<view class="a-address" v-for="(item,index) in addressList" :key="index">
				<view class="left-text" :class="item.isDefault? 'active':''" @tap="selectTap(item.id)">
					<view class="name-tel">
						{{item.name}} {{item.tel}}
					</view>
					<view class="address-box">
						{{item.address}}
					</view>
					<view class="address-box">
						{{item.home}}
					</view>
				</view>
				<view class="right-edit" @tap="editAddess(item.id)"></view>
			</view>
		</view>
		<view class="bottom-box">
			<view class="add-btn" @tap="addAddess">新增收货地址</view>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				addressList: []
			}
		},
		onShow() {
			/*获取用户保存的地址信息*/
			this.getAddressList()
		},
		methods: {
			async getAddressList() {
				const result = await this.$myRequest({
					url: '/api/address/query'
				})
				this.addressList  = result 
			},
			selectTap(id) {
				console.log("tap item id:" + JSON.stringify(id));
			},
			editAddess(id) {
				console.log("edit item id:" + id);
			},
			addAddess() {
				/*跳转到添加新地址页面*/
				uni.navigateTo({
					url: '/pages/address/address'
				})
			}
		}
	}
</script>

